import * as events from './event-bus';
import * as env from './env';
import * as LDClient from 'ldclient-js';
import * as store from './store';

let ldclient = null;

const envKeyMap = {
	dev: '5b522a204892e62d90dad00b',
	qa: '5b5749ea6c774e2ec9e8ee20',
	stage: '5b36942fa6f97714d8295171',
	prod: '5b16ba9ceb049c319c453c93'
};

export function setupFeatureFlags(idmData) {
	const idm= store.get('idm');
	const envName = env.environmentFromIdmGatewayUrl(idm.idGatewayUrl);
	const envLDKey = envKeyMap[envName];

	// LaunchDarkly setup based on https://docs.launchdarkly.com/docs/js-sdk-reference
	const user = {
		'key': idm.flId, //TODO- replace employeeId with personId
		'custom' : {
			'orgId': idmData.userContext.OrganizationId
		}
	};
	ldclient= LDClient.initialize(envLDKey, user);
	ldclient.on('ready', d => events.emit('feature-flags-ready', {checkState}));
}

/**
 * @param {*} featureKey - flag to check (key)
 * @param {*} featureValue - Optional, for comparing multivariate values
 * If key doesn't exist, ldclient will return false as value of featureKey
 * @returns If key does exist, will return whether it matches featureValue
 */
export const checkState = (featureKey, featureValue = true) => {
	return (ldclient.variation(featureKey, false) === featureValue);
};

